<template>
  <div class="container p-3">
    <PagesTitle title="ApiJsonView" />
    <div class="mt-5">
      <h3 class="mt-2 mb-2">文件列表</h3>
      <div class="row pages-files overflow-auto border border-info rounded">
        <div class="list-group w-100">
          <a
            @click="loadFile(file)"
            class="list-group-item list-group-item-action"
            :class="checkedFile==file ? 'bg-primary text-light' : ''"
            v-for="file in fileList"
            :key="file"
          >{{file}}</a>
        </div>
      </div>
      <h3 class="mt-2 mb-2" >文件内容</h3>
      <div class="row">
        <textarea
          v-text="fileContent"
          class="pages-code overflow-auto border border-secondary mt-1 w-100 rounded"
        ></textarea>
      </div>
    </div>
  </div>
</template>

<script>
import "bootstrap/dist/css/bootstrap.min.css";
import PagesTitle from "../components/PagesTitle";
const jsonFileUrl = process.env.VUE_APP_APIURL_JSONFILE;

export default {
  name: "ApiJsonView",
  data() {
    return {
      fileList: [],
      fileContent: "",
      checkedFile: ""
    };
  },
  components: {
    PagesTitle
  },
  methods: {
    loadFile(file) {
      this.checkedFile = file;
      fetch(`${jsonFileUrl}/${file.substring(0, file.lastIndexOf(".json"))}`)
        .then(res => res.json())
        .then(data => {
          this.fileContent = JSON.stringify(data, null, 4);
        });
    }
  },
  mounted() {
    fetch(`${jsonFileUrl}/api/list`)
      .then(res => res.json())
      .then(data => {
        this.fileList = data;
      });
  }
};
</script>

<style scoped>
.pages-code {
  height: 520px;
}

.pages-files {
  height: 220px;
}
</style>