<template>
  <div class="p-3">
    <PagesTitle title="Bookmarks" />
    <div class="row mt-5 p-1">
      <input
        v-model="serachTxt"
        type="text"
        @keyup="serach"
        class="form-control bm-serach w-20"
        placeholder="Search Bookmark ..."
      />
    </div>

    <div class="row d-flex justify-content-center">
      <span
        :class="checkedType===type ? 'badge-primary':'badge-info'"
        @click="typeFilter(type)"
        v-for="type in typeList"
        :key="type"
        class="badge p-2 m-2 cursor-pointer"
      >{{type}}</span>
    </div>

    <div class="row d-flex justify-content-center">
      <span
        :class="checkedTag===tag ? 'badge-primary':'badge-secondary'"
        @click="tagFilter(tag)"
        v-for="tag in tagList"
        :key="tag"
        class="badge p-1 m-1 cursor-pointer"
      >{{tag}}</span>
    </div>
    <div class="row">
      <ul class="list-group list-group-item-info overflow-hidden mt-3 w-100">
        <li
          v-for="(item,index) in bmList"
          :key="index"
          class="list-group-item overflow-hidden bm-item text-center"
        >
          <a :href="item.url" class="text-info">{{item.title}} # {{item.url}}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import "bootstrap/dist/css/bootstrap.min.css";
import PagesTitle from "../components/PagesTitle";
const pageJsonUrl = process.env.VUE_APP_APIURL;
export default {
  name: "Bookmark",
  components: { PagesTitle },
  data() {
    return {
      oldBmList: [],
      typeList: [], // 分类集合
      tagList: [], // 标签集合
      bmList: [], //书签集合
      checkedType: "",
      checkedTag: "",
      serachTxt: ""
    };
  },
  methods: {
    typeFilter(type) {
      this.checkedTag = "";
      this.checkedType = this.checkedType === type ? "" : type;
      this.bmList = this.oldBmList.filter(
        bm => this.checkedType === "" || bm.type === type
      );
    },
    tagFilter(tag) {
      this.checkedType = "";
      this.checkedTag = this.checkedTag === tag ? "" : tag;
      this.bmList = this.oldBmList.filter(
        bm => this.checkedTag === "" || bm.tag === tag
      );
    },
    serach() {
      this.checkedTag = "";
      this.checkedType = "";
      let txt = this.serachTxt.toUpperCase();
      this.bmList = this.oldBmList.filter(
        ({ title, url, type, tag }) =>
          txt === "" ||
          title.toUpperCase().indexOf(txt) !== -1 ||
          url.toUpperCase().indexOf(txt) !== -1 ||
          tag.toUpperCase().indexOf(txt) !== -1 ||
          type.toUpperCase().indexOf(txt) !== -1
      );
    }
  },
  mounted() {
    fetch(`${pageJsonUrl}/chrome.bookmark.json`)
      .then(res => res.json())
      .then(data => {
        let eachBm = (object, isRoot, type, tag) => {
          let { b: title, c: url, a: children } = object;
          if (url) {
            this.oldBmList.push({ title, url, type, tag });
            this.bmList.push({ title, url, type, tag });
          } else {
            !isRoot && this.tagList.push(title);
            if (children) {
              children.forEach(item => {
                eachBm(item, false, type, title);
              });
            }
          }
        };
        data.a.forEach(n => {
          this.typeList.push(n.b);
          eachBm(n, true, n.b);
        });
      });
  }
};
</script>

<style scoped>
.cursor-pointer {
  cursor: pointer;
}

.bm-item {
  white-space: nowrap;
  text-overflow: ellipsis;
}

.bm-serach {
  width: 100%;
}
</style>
